// Simple Reset
*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    // This define what 1rem is (1rem = 10px) (10px/16px = 62.5% = 1rem)
    font-size: 62.5%;

    @include respond(tab-land) {        // width < 900?
        font-size: 56.25%;              // 1rem = 9px (9/16) = 56.25% 
    }

    @include respond(tab-port) {        // width < 600?
        font-size: 50%;                 // 1rem = 8px (8/16) = 50%
    }

    @include respond(big-desktop) {     // 1rem = 12px (12/16) = 75%
        font-size: 75%;
    }
}

body {
    box-sizing: border-box;
    padding: 3rem;

    @include respond(tab-port) {
        padding: 0;
    }
}