@mixin clearfix {
    &::after {
        content: "";
        display: table;
        clear: both;
    }
}

@mixin absCenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

// MEDIA QUERIES MANAGER
// ================================================================
// 0        -       600px:           Phone
// 600px    -       900px:           Table and Portrait Mode
// 900px    -       1200px:          Tablet and Landscape
// [1200px  -       1800px]:         Is where normal styles apply
// 1800px + :                        Big Desktop       
// ================================================================
// $breakpoint argument choices:
// - phone
// - tab-port
// - tab-land
// - big-desktop
// ORDER: Base + Typography > general layout + grid > layout > components
// 1em = 16px
@mixin respond($breakpoint) {
    @if($breakpoint == phone) {
        @media(max-width: 37.5em) { @content; }        // 600px/16px
    } @else if($breakpoint == tab-port) {
        @media(max-width: 56.25em) { @content; }        // 900px/16px   
    } @else if($breakpoint == tab-land) {
        @media(max-width: 75em) { @content; }           // 1200px/16px
    } @else if($breakpoint == big-desktop) {
        @media(min-width: 112.5em) { @content; }        // 1800px/16px
    }
}

// BEM (Block Element Modifier)
// Usage:   @include element('element-one') {} 
//          @include modifier('outline') {}

@mixin element($e) {
    &__#{$e} {
        @content;
    }
}

@mixin modifier($m) {
    &--#{$m} {
        @content;
    }
}

// ================================================================

@mixin check() {
    border: 1px dashed orangered;
}

@mixin init() {
    margin: inherit;
    padding: inherit;
}